.row {
  background-color: rgb(0, 0, 0);
  color: #ffffff;
  padding: 2rem 0;
}
.row > h2 {
  padding-left: 1rem;
}
.slider {
  display: flex;
  position: relative;
}
.slider__arrow {
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 2rem;
  background-clip: content-box;

  position: absolute;
  top: 0;
  width: 4rem;
  height: 100%;
  cursor: pointer;

  visibility: hidden;
}
.slider:hover .slider__arrow {
  visibility: visible;
  transition: all 200ms ease-in;
}
.slider__arrow:hover {
  background-color: rgba(0, 0, 0, 0.3);
}
.arrow-left {
  left: 0;
}
.arrow-right {
  right: 0;
}
.arrow {
  z-index: 1;
  transition: all 200ms ease-in;
}
.arrow:hover {
  transform: scale(1.3);
}

.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 1rem;
  scroll-behavior: smooth;
}
.row__posters::-webkit-scrollbar {
  display: none;
}
.row__poster {
  object-fit: contain;
  width: 100%;
  max-height: 10rem;
  cursor: pointer;
  border-radius: 4px;
  transition: all 250ms ease-in;
}
.row__poster:not(:last-child) {
  margin-right: 0.5rem;
}
.row__poster.row__poster-large {
  max-height: 20rem;
}
.row__poster:hover {
  transform: scale(1.08);
}
.swiper-slide {
  padding: 0.5rem;
}
.swiper-pagination {
  text-align: right;
}

.swiper-pagination-bullet {
  background: gray;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background: white;
}

.swiper-button-prev {
  color: white;
}

.swiper-button-next {
  color: white;
}

.swiper-button-next:after, .swiper-button-prev:after{
  font-size: 1.3rem;
  font-weight: 600;
}
